.projects-container{
    min-height: 100vh;
    display: flex;
    align-items: center;
    flex-direction: column;
    
    
}

.projects-container h2{
    margin-top: 125px;
    font-weight: 600px;
    color: rgb(129, 32, 28);
    font-size: 60px;
}

.projects-container hr{
    border: none;
    background-color: rgb(129, 32, 28);
    height: 5px;
    border-radius: 10px;
    margin-bottom: 5px;
}

@media (max-width: 640px) {
    .projects-container h2{
        font-size: 2.75rem;
        margin-top: 100px;
    }
}